.About{
    width: 100%;
    height: 100vh;

}

.Dark{
    background-image: linear-gradient(to top,#32217e60, #1b161e), url("../../assets/images/portfolio-background.jpg") ;
}


.Light{
    background-image: linear-gradient(to top, #acebeb93, #417c7c), url("../../assets/images/portfolio-background.jpg") ;
}

.SectionsContainer{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.LeftSection{
    width: 25%;
    height: 50%;
    text-align: center;
}

.RightSection{
    
    width: 25%;
    height: 50%;
    text-align: center;    
}

@media (max-width: 500px) {
    .LeftSection{
        padding-left: 10px;
        box-sizing: border-box;
        width: 40%;
    }
    
    .RightSection{
        padding-right: 10px;
        box-sizing: border-box;
        width: 40%;
        word-break: break-all;
    }
}

